<template>
  <div class="new-boid-container" style="position: relative;">
    <canvas id="new-boid-canvas" style="z-index: 9999;" width="800" height="600"></canvas>
    <canvas width="800" height="600" class="offscreen" style="z-index: 9999; display: none;"></canvas>
    <div class="new-boid-gui-container">
      <div id="new-boid-gui"></div>
    </div>
  </div>
</template>
<script>
import NewBoid from "./NewBoidInteractive";
export default {
  components: {},
  data() {
    return {
      initialised: false
    };
  },
  methods: {
    init() {
      if (this.initialised) return;
      this.initialised = true;

      const newBoid = new NewBoid();

      this.$on("hook:beforeDestroy", () => {
        newBoid.destroy();
      });
    }
  },
  mounted() {
    this.init();
  },
  created() {}
};
</script>
<style lang="scss">
#new-boid-gui {
  position: absolute;
  top: 0;
  right: 0;
}

#new-boid-canvas {
}

.new-boid-container {
}

.new-boid-gui-container {
  width: 300px;
}
</style>
