<template>
  <span class="control-item">
    <a
      style="display: inline-block; max-width: 25px; max-height: 25px;"
      @click.prevent="
        $root.$emit('control-clicked', { control, controlKey, type })
      "
    >
      <svg v-if="'svg' in control" v-bind="control.svg">
        <path v-bind="control.path" :fill="getFill"></path>
      </svg>
      <i
        :style="iconColor"
        v-else-if="'icon' in control"
        :class="control.icon"
      ></i>
    </a>
  </span>
</template>
<script>
export default {
  props: {
    control: {
      type: Object,
      required: true
    },
    controlKey: {
      type: String,
      required: true
    },
    type: {
      type: String,
      required: true
    }
  },
  computed: {
    iconColor() {
      return "icon" in this.control
        ? {
            color: this.getFill
          }
        : null;
    },
    /**
     * Get a color for path fill attribute
     * If the control is active then use activeColor from config
     * or default to 'green'
     * If it is inactive then use inactiveColor or default to '#ccc'
     * @return string
     */
    getFill() {
      return this.control.active
        ? this.control.activeColor || "green"
        : this.control.inactiveColor || "#ccc";
    }
  }
};
</script>
<style lang="scss" scoped></style>
