import * as d3 from "d3";
import Vec2 from "@/assets/scripts/vec2.js";
import * as dat from "dat.gui";

let NewBoidInteractive = function() {
  const canvas = document.getElementById("new-boid-canvas");
  const context = canvas.getContext("2d");
  const offscreen = document.querySelector(".offscreen");
  const offscreenContext = offscreen.getContext("2d");
  const gui = new dat.GUI({
    autoPlace: false
  });

  const guiContainer = document.getElementById("new-boid-gui");
  guiContainer.appendChild(gui.domElement);

  offscreenContext.globalAlpha = 0.85;

  this.width = 800;
  this.height = 600;
  this.numBoids = 300;
  this.separationDistance = 30;
  this.maxVelocity = 2;
  this.separationForce = 0.03;
  this.alignmentForce = 0.03;
  this.cohesionForce = 0.03;
  this.startingPosition = "Random";
  this.coloring = "By Movement";
  this.boids;
  this.flockmateRadius = 60;

  gui.add(this, "flockmateRadius", 0, 500).step(1);
  gui.add(this, "separationDistance", 0, 100).step(1);
  gui.add(this, "maxVelocity", 0, 5).step(0.25);
  gui.add(this, "cohesionForce", 0, 0.25);
  gui.add(this, "alignmentForce", 0, 0.25);
  gui.add(this, "separationForce", 0, 0.25);
  gui
    .add(this, "numBoids", 1, 600)
    .step(1)
    .onChange(this.restart);
  gui
    .add(this, "startingPosition", [
      "Random",
      "CircleIn",
      "CircleRandom",
      "Sine",
      "Phyllotaxis"
    ])
    .onChange(this.restart);
  gui.add(this, "coloring", ["Rainbow", "By Movement"]);

  d3.select("canvas").on("click", () => {
    var xy = d3.mouse(this);
    this.boids.push({
      color: d3.interpolateRainbow((this.boids.length / 10) % 1),
      position: new Vec2(xy[0], xy[1]),
      velocity: this.randomVelocity(),
      last: []
    });
  });

  this.restart = function() {
    offscreenContext.clearRect(0, 0, this.width, this.height);
    context.clearRect(0, 0, this.width, this.height);
    this.boids = this["initialize" + this.startingPosition]();
    this.boids.forEach((b, i) => {
      b.color = d3.interpolateRainbow(i / this.numBoids);
      b.last = [];
    });
  };

  gui.add(this, "restart");

  this.tick = () => {
    offscreenContext.clearRect(0, 0, this.width, this.height);
    offscreenContext.drawImage(canvas, 0, 0, this.width, this.height);
    context.clearRect(0, 0, this.width, this.height);
    context.drawImage(offscreen, 0, 0, this.width, this.height);

    this.boids.forEach(b => {
      var forces = {
        alignment: new Vec2(),
        cohesion: new Vec2(),
        separation: new Vec2()
      };

      b.acceleration = new Vec2();

      this.boids.forEach(b2 => {
        if (b === b2) return;

        var diff = b2.position.clone().subtract(b.position),
          distance = diff.length();

        if (distance && distance < this.separationDistance) {
          forces.separation.add(
            diff.clone().scaleTo(-1 / distance)
          ).active = true;
        }

        if (distance < this.flockmateRadius) {
          forces.cohesion.add(diff).active = true;
          forces.alignment.add(b2.velocity).active = true;
        }
      });

      for (var key in forces) {
        if (forces[key].active) {
          forces[key]
            .scaleTo(this.maxVelocity)
            .subtract(b.velocity)
            .truncate(this[key + "Force"]);
          b.acceleration.add(forces[key]);
        }
      }

      if (this.coloring === "By Movement") {
        b.last.push(
          b.acceleration.length() /
            (this.alignmentForce + this.cohesionForce + this.separationForce)
        );
        if (b.last.length > 20) {
          b.last.shift();
        }
      }
    });

    this.boids.forEach(this.updateBoid);
    requestAnimationFrame(this.tick);
  };

  this.updateBoid = b => {
    b.position.add(b.velocity.add(b.acceleration).truncate(this.maxVelocity));

    if (b.position.y > this.height) {
      b.position.y -= this.height;
    } else if (b.position.y < 0) {
      b.position.y += this.height;
    }

    if (b.position.x > this.width) {
      b.position.x -= this.width;
    } else if (b.position.x < 0) {
      b.position.x += this.width;
    }

    context.beginPath();
    if (this.coloring === "Rainbow") {
      context.fillStyle = b.color;
    } else {
      context.fillStyle = d3.interpolateWarm(d3.mean(b.last));
    }
    context.arc(b.position.x, b.position.y, 2, 0, 2 * Math.PI);
    context.fill();
  };

  this.initializeRandom = () => {
    return d3.range(this.numBoids).map((d, i) => {
      return {
        position: new Vec2(
          Math.random() * this.width,
          Math.random() * this.height
        ),
        velocity: this.randomVelocity()
      };
    });
  };

  this.initializePhyllotaxis = () => {
    return d3.range(this.numBoids).map((d, i) => {
      var Î¸ = Math.PI * i * (Math.sqrt(5) - 1),
        r = (Math.sqrt(i) * 200) / Math.sqrt(this.numBoids);

      return {
        position: new Vec2(
          this.width / 2 + r * Math.cos(Î¸),
          this.height / 2 - r * Math.sin(Î¸)
        ),
        velocity: this.radialVelocity(i / this.numBoids)
      };
    });
  };

  this.initializeSine = () => {
    return d3.range(this.numBoids).map(i => {
      var angle = (2 * Math.PI * i) / this.numBoids,
        x = (this.width * i) / this.numBoids,
        y = this.height / 2 + (Math.sin(angle) * this.height) / 4;

      return {
        position: new Vec2(x, y),
        velocity: this.radialVelocity(i / this.numBoids)
      };
    });
  };

  this.initializeCircleIn = () => {
    return d3.range(this.numBoids).map(i => {
      var angle = (i * 2 * Math.PI) / this.numBoids,
        x = 200 * Math.sin(angle),
        y = 200 * Math.cos(angle);

      return {
        position: new Vec2(x + this.width / 2, y + this.height / 2),
        velocity: new Vec2(-x, -y).scale(this.maxVelocity)
      };
    });
  };

  this.initializeCircleRandom = () => {
    return d3.range(this.numBoids).map(i => {
      var angle = (i * 2 * Math.PI) / this.numBoids,
        x = 200 * Math.sin(angle),
        y = 200 * Math.cos(angle);

      return {
        position: new Vec2(x + this.width / 2, y + this.height / 2),
        velocity: this.randomVelocity().scale(this.maxVelocity)
      };
    });
  };

  this.randomVelocity = () => {
    return new Vec2(1 - Math.random() * 2, 1 - Math.random() * 2).scale(
      this.maxVelocity
    );
  };

  this.radialVelocity = p => {
    return new Vec2(Math.sin(2 * Math.PI * p), Math.cos(2 * Math.PI * p)).scale(
      this.maxVelocity
    );
  };

  this.destroy = () => {
    gui.destroy();
  };

  this.restart();
  requestAnimationFrame(this.tick);
};

export default NewBoidInteractive;
