import katex from "katex";
import { replaceAll } from "@/utils/utils";

const katexRenderer = (el, binding) => {
  const eq = binding.value;
  // If render modifier was provided then
  // we render the equation directly.
  // Otherwise we have to replace text
  if (binding.modifiers.render) {
    el.innerHTML = katex.renderToString(eq);
    return;
  }

  // We are using a $$equation$$ pattern to find an equation to replace
  const match = eq.match(/\$\$(.*?)\$\$/g);

  // If there was any match and equation was found
  // we loop through found equations
  // otherwise just return original string
  el.innerHTML = match
    ? match.reduce((eq, str) => {
        // First get an equation text so we can pass it to katex
        const equationStr = replaceAll(str, "$$", "");
        // Then we replace the equation string with the one provided by katex
        eq = eq.replace(str, katex.renderToString(equationStr));
        return eq;
      }, eq)
    : eq;
};

export default {
  inserted: katexRenderer,
  componentUpdated: katexRenderer
};
