<template>
  <div class="topic-link-container" v-if="linkNotActive && item">
    <a @click.prevent="linkNotActive = false">{{ item.link.label }}</a>
    <controls :controls="linkControls"></controls>
  </div>
  <div v-else>
    <div :key="index" v-for="(item, index) in content || linkTopicContent" style="padding=3rem">
      <topic-content
        v-if="'link' in item && depth < maxDepth"
        :depth="depth + 1"
        :maxDepth="maxDepth"
        link
        :item="item"
        :topic="linkTopic"
        :content="linkTopicContent"
        :topics="topics"
        @click.prevent="$emit('change-topic', item.link.id)"
      >{{ item.link.label }}</topic-content>

      <div
        v-else-if="'preface' in item"
        class="preface"
        v-katex="item.preface"
        v-html="item.preface"
      ></div>

      <div v-else-if="'image' in item && mediaControls.image.active">
        <figure class="content-figure">
          <img :src="item.image.url">
          <figcaption v-if="item.image.caption">{{ item.image.caption }}</figcaption>
        </figure>
      </div>

      <p v-else-if="'text' in item">
        <!--<h1 v-if="'title' in item">item.title</h1> can I do something similar?-->
        <span v-html="item.text"></span>
        <br>
      </p>

      <div v-else-if="'video' in item">
        <TopicVideo :item="item" :mediaControls="mediaControls"/>
      </div>
      <!-- Interactive! -->
      <!-- <component v-else-if="'interactive' in item" :is="item.interactive.component"></component> -->
      <div
        style="margin: 1rem 0;"
        v-else-if="'equation' in item && mediaControls.equation.active"
        v-katex.render="item.equation"
      ></div>

      <component
        v-else-if="'component' in item && mediaControls.interactive.active"
        :is="item.component"
      />
      <p></p>
    </div>
  </div>
</template>
<script>
import TopicVideo from "./TopicVideo";
import Controls from "@/components/Controls/Controls";
import NewBoid from "@/components/Interactive/NewBoid";

export default {
  name: "TopicContent",
  components: {
    NewBoid,
    Controls,
    TopicVideo
  },
  props: {
    content: {
      type: Array,
      default: () => ({})
    },
    topic: {
      type: Object,
      default: () => ({})
    },
    topics: {
      type: Object,
      default: () => ({})
    },
    depth: {
      type: Number,
      default: 0
    },
    maxDepth: {
      type: Number,
      default: 5
    },
    item: {
      type: Object
    },
    link: {
      type: Boolean,
      default: false
    }
  },
  inject: ["controls"],
  data() {
    return {
      linkNotActive: this.link
    };
  },
  computed: {
    mediaControls() {
      return this.controls.mediaTypeControls;
    },
    contentControls() {
      return this.controls.contentTypeControls;
    },
    /**
     * Get a topic based on the id of the link provided
     * @return {object|null}
     */
    linkTopic() {
      return this.topics && this.item
        ? Object.values(this.topics).find(
            topic => topic.id === this.item.link.id
          )
        : null;
    },
    linkTopicContent() {
      return this.linkTopic ? this.linkTopic.content : null;
    },
    /**
     * If there is a topic then return tags for it
     * @return {array}
     */
    tags() {
      return this.linkTopic ? this.linkTopic.tags : [];
    },
    /**
     * Create new controls object for each topic link
     * Loop through topic tags and return required controls
     * @return {object} - controls
     * @property {object} - mediaTypeControls
     * @property {object} - contentTypeControls
     */
    linkControls() {
      // First we loop through the tags specified for the topic
      return this.tags.reduce(
        (acc, tag) => {
          // There are 2 types of controls, but tags in the topic
          // are all in one array, therefore we have to search in both control types
          Object.entries(this.controls).forEach(([key, value]) => {
            // We have a match! Tag was found in one of the control types
            if (this.controls[key].hasOwnProperty(tag)) {
              acc[key][tag] = value[tag];
            }
          });
          return acc;
        },
        { mediaTypeControls: {}, contentTypeControls: {} }
      );
    }
  }
};
</script>
<style lang="scss" scoped></style>
