<template>
  <div class="home">
    <div class="container">
      <section class>
        <div v-if="selectedTopic">
          <Topic
            @breadcrumb-clicked="addBreadcrumb"
            :breadcrumbs="getBreadcrumbs"
            @topic-clicked="topicClicked"
            :topic="selectedTopic"
            :is-selected-topic="true"
          ></Topic>
        </div>
        <div class="topics">
          <template v-if="topics.length">
            <swiper ref="topicsSwiper" :options="$options.swiperOptions">
              <!-- slides -->
              <swiper-slide v-for="topic in topics" :key="topic.id">
                <Topic @topic-clicked="topicClicked" :topic="topic"></Topic>
              </swiper-slide>

              <!-- Optional controls -->
              <div class="swiper-pagination" slot="pagination"></div>
              <div class="swiper-button-prev" slot="button-prev"></div>
              <div class="swiper-button-next" slot="button-next"></div>
            </swiper>
          </template>
        </div>
        <div class="columns">
          <div class="column has-text-left">
            <!-- <breadcrumbs :breadcrumbs="getBreadcrumbs" @breadcrumb-clicked="addBreadcrumb" ></breadcrumbs> -->
          </div>
          <div class="column has-text-right">
            <Controls slot="controls" :controls="controls"/>
          </div>
        </div>
      </section>
      <section class="section">
        <div class="content" v-if="selectedTopic">
          <topic-content
            :topic="selectedTopic"
            :topics="nodes"
            :link="false"
            :content="selectedTopic.content"
            :depth="0"
          ></topic-content>
        </div>
        <div class="content">
          <div class="comments" v-if="showDisqus">
            <vue-disqus
              :url="'http://localhost:8080/topic/' + selectedTopicId"
              :identifier="selectedTopicId"
              :title="selectedTopicTitle"
              shortname="itss2019"
            ></vue-disqus>
          </div>
        </div>
      </section>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import get from "lodash/get";
import controlOptions from "@/config/controlOptions.js";
import Topic from "@/components/Topic/Topic";
// import Breadcrumbs from "@/components/Breadcrumbs";
import Controls from "@/components/Controls/Controls";
import TopicContent from "@/components/Topic/TopicContent";
import topicsjson from "@/assets/itSS.json";

const INITIAL_TOPIC_ID = "iss";

export default {
  name: "home",
  components: {
    Topic,
    TopicContent,
    Controls
  },
  data() {
    return {
      nodes: [],
      breadcrumbs: [],
      branch: "Introduction to sustainability science",
      history: "Introduction to sustainability science",
      selectedTopicId: INITIAL_TOPIC_ID,
      additionalContent: "",
      additionalContentPointer: 1,
      controls: controlOptions,
      showDisqus: true
    };
  },
  watch: {},
  provide() {
    const obj = {
      initialTopicId: INITIAL_TOPIC_ID
    };

    Object.defineProperty(obj, "topics", {
      enumerable: true,
      get: () => this.nodes
    });

    Object.defineProperty(obj, "controls", {
      enumerable: true,
      get: () => this.controls
    });

    return obj;
  },
  computed: {
    getSelectedTopic() {
      return Object.values(this.nodes).find(item => {
        return item.id === this.selectedTopicId;
      });
    },
    /**
     * Topics need to be filtered by controls selected
     * @return {array} - filtered topics
     */
    topics() {
      const { topics } = this.getTopics;
      // First we need to get an array of content type controls
      // We are going to loop through it and check if there is a match between a topic tag and control key
      const contentTypeControls = Object.entries(
        this.controls.contentTypeControls
      );

      // Filter all the topics
      return topics.filter(topic =>
        // Check if any of the controls is active for any tag
        topic.tags.some(tag => {
          for (let [key, value] of contentTypeControls) {
            // If we have a match and control is active
            // then topic should be displayed
            if (key === tag && value.active) {
              return true;
            }
          }
          // Topic doesn't have any active tags so we filter it out
          return false;
        })
      );
    },
    selectedTopic() {
      const { selectedTopic } = this.getTopics;
      return selectedTopic;
    },
    selectedTopicTitle() {
      return this.selectedTopic.title || this.selectedTopic.Purpose;
    },
    selectedTopicPurpose() {
      return this.selectedTopic.Purpose;
    },
    selectedTopicContent() {
      return this.selectedTopic.content[0].preface;
    },
    getBreadcrumbs() {
      return this.breadcrumbs.slice(-3);
    },
    getTopics() {
      const topics = Object.values(this.nodes);
      const selectedTopic = topics.find(
        item => item.id === this.selectedTopicId
      );
      // If there is no selected topic yet we just return object with no topics
      if (!selectedTopic) return { selectedTopic: null, topics: [] };

      // Get topic links from currently selected topic
      const topicLinks = selectedTopic.content.reduce((links, item) => {
        if (item.hasOwnProperty("link")) {
          links.push(item.link.id || item.link);
        }
        return links;
      }, []);

      // Filter out topics for selected topic links
      const linkedTopics = topics.filter(topic =>
        topicLinks.includes(topic.id)
      );

      return {
        selectedTopic,
        topics: linkedTopics
      };
    }
  },
  methods: {
    /**
     * Update active state of a control
     * @param {object}
     * @property {object} - control object
     * @property {string} - controlKey: video, image, etc.
     * @property {string} - type -  type of the control: contentTypeControl | mediaTypeControl
     */
    updateControlState({ control, controlKey, type }) {
      this.controls[type][controlKey].active = !control.active;
    },
    /**
     * Handler for topic slide click
     * @param {object|string} param
     */
    topicClicked(param, pushTopicToHistory = true) {
      /**
       * Param passed can be a topic object
       * or it can be a string id
       * if it is a string we have to find the topic
       */
      let topic = param;
      if (typeof param === "string") {
        topic = Object.values(this.nodes).find(item => item.id === param);
      }
      // stop if no topic found, for instance if user entered id manually
      if (!topic) return;

      const id = topic.id;
      this.selectedTopicId = id;
      this.addBreadcrumb(topic);
      this.resetDisqus();

      pushTopicToHistory &&
        this.$router.push({
          name: "Topic",
          params: {
            id
          }
        });
    },
    /**
     * Add a new breadcrumb
     * @param {object} topic
     */
    addBreadcrumb(topic) {
      // Filter out newly added breadcrumb from existing breadcrumbs so we don't have a repetition
      // Also, add a new topic to the end of breadcrumbs
      this.breadcrumbs = this.breadcrumbs
        .filter(item => item.id !== topic.id)
        .concat(topic);
      this.selectedTopicId = topic.id;
    },
    // Initial breadcrumb will default to the first topic selected
    setInitialBreadcrumb() {
      this.breadcrumbs.push(this.selectedTopic);
    },
    /**
     * Set options for the topic swiper
     * http://idangero.us/swiper/api/#parameters
     */
    setSwiperOptions() {
      this.$options.swiperOptions = {
        slidesPerView: 3,
        spaceBetween: 5,
        mousewheel: true,
        pagination: {
          el: ".swiper-pagination",
          type: "progressbar",
        },
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev"
        },
        slidesPerView: 3,
        breakpoints: {
          640: {
            slidesPerView: 3
          },
          380: {
            slidesPerView: 3
          }
        }
      };
    },
    fetchNotes() {
      this.nodes = topicsjson;
      this.setInitialBreadcrumb();
    },
    onpopstate() {
      const id = get(this, "$route.params.id");
      if (!id) {
        this.selectedTopicId = INITIAL_TOPIC_ID;
        this.breadcrumbs = [];
        return;
      }

      try {
        if (id === this.breadcrumbs[this.breadcrumbs.length - 1].id) {
          this.breadcrumbs.pop();
          this.selectedTopicId = id;
          this.resetDisqus();
        } else {
          this.topicClicked(id, false);
        }
      } catch (error) {
        console.error("Error", error);
      }
    },
    resetDisqus() {
      this.showDisqus = false;
      this.$nextTick(() => {
        this.showDisqus = true;
      });
    }
  },
  beforeDestroy() {
    window.removeEventListener("popstate", this.onpopstate);
  },
  created() {
    this.$root.$on("control-clicked", this.updateControlState);
    this.setSwiperOptions();
    this.fetchNotes();
    window.addEventListener("popstate", this.onpopstate);
  }
};
</script>
